  background-repeat: no-repeat;
  background-attachment: fixed;

}
h2{
  color: #dededef7;
}

#root {
  margin: 0 30px;
  position: relative;
  margin: auto;
  /* min-height: 100vh; */
  max-width: 80%;
  min-width: 60%;
}
.loading{
  margin-left: 30px;
}

.error{
  margin-left: 30px;
  color: red;
  font-weight: bold;
}

.TodoCounter {
  font-size: 24px;
  text-align: center;
  margin: 0;
  padding: 48px;
}
.TodoCounter-grandContainer{
  border-radius:15px;
  background-color: white;
  height: 170px;
  text-align: center;
  /* margin-top: -32px; */
  margin-bottom: 80px;
  box-shadow: 0px 10px 100px 100px #ffffff10;
  width: 100%;
  height: 320px;

  color: white;
  font-size: 20px;
  font-weight: 400;
}
.TodoCounter-container{
  text-align: center;
  display:flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  margin-top: 24px;
}
.TodoCounter-text{
  position: relative;
  top: -240px;
  font-weight: 400;
  font-size: 24px;
  margin-top: 48px;
  background-color: #341046;
  padding: 12px;
  /* border-radius: 15px; */
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container-completed,
.container-total{
  position:relative;
  top:-190px;
  display: grid;
  place-items: center;
  border-radius:15px;
  max-width: 80px;
  min-width: 80px;
  /* height: 100px; */
  text-align: center;
  padding:12px;
  margin: 12px;
  bottom: -30px;
  background-color: #341046;
  /* border: 1px white solid; */
  /* box-shadow: 0px 10px 10px 1px #ffffff10; */
}

.Icon-completed,
.Icon-total{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  /* background-color: #d55303; */
  border: 3px solid white;
  border-radius: 50%;
  font-size: 48px;
  padding: 8px;
  margin-top: 8px;
  margin-bottom: 8px;
  color: white;
}.counter-text{
  color: white;
}




.responsive-banner {
