.TodoSearch {
  background: #dadada;
  border: 2px solid #202329;
  border-radius: 20px;
  height: 35px;
  width: 80%;

  font-size: 15px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  position: relative;
  display: block;
  margin : 0 auto;
}

.TodoSearch::-webkit-input-placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #341046;
}
ul {
    margin: 0;
    padding: 25px 0 15px 0;
    list-style: none;
}
.TodoList{

  margin-top: -15px;
  border-radius: 15px;
  background-color: #ffffff;
  padding: 10px;
  margin-bottom: 100px;
  /* max-width: 70%; */
}
.TodoList>ul>p{
  text-align: center;
  font-weight:500;
  font-size: 24px;
}
.CreateTodoButton {
    background-color: #e27b00;
    box-shadow: 0px 5px 25px rgba(200, 58, 11, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    -webkit-transform: rotate(0);
  
            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
  }
  
  .CreateTodoButton:hover {
    -webkit-transform: rotate(225deg);
            transform: rotate(225deg);
  }
