    transform: rotate(-224deg);
    content: attr(title);
    font-size: 12px;
    position: absolute;
    top: -50%; 
    left: 100%;
  } */
.TodoItem {
    background-color: #dededef7;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid rgb(191, 191, 191);
    margin: 20px auto;
    width: 90%;
    border-radius: 15px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    width: -moz-fit-content;
    word-wrap: break-word;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    top: 12px;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
    font-size: 30px;
    color: #341046;
  }
  .Icon-check--active {
    color: #87b704;
  }
  
  .Icon-delete {
    position: absolute;
    /* top: -24px; */
    right: 0;
    font-size: 30px;
    color: #8C001A;
  }
  .Icon-delete:hover {
    color: #c60226;
  }
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
form {
    border-radius: 15px;
    width: 90%;
    max-width: 300px;
    background-color: #dededef7;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    border-radius: 15px;
    background-color: #fff;
    border: 2px solid #341046;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  /* textarea:focus {
    outline-color: #000;
  } */
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
    border-radius: 15px;

  }
  
  .TodoForm-button--add {
    background: #d55303;
    color: white;
    /* box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5); */
  }
  .TodoForm-button--add:hover {
    background: #ab4302;
  }
  
  .TodoForm-button--cancel {
    color: white;
    background: #4b1765;
  }
  .TodoForm-button--cancel:hover {
    background: #341046;
  }
.LoadingTodo-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 5 50 rgba(32,35,41,0.15);
    width: 90%;
    margin: 20px auto;
    border-radius: 15px;
}.LoadingTodo-text{
    margin: 24px 0px 24px 24px;
    width: cal(100% -120px);
    font-size: 18px;
    color: rgb(149, 149, 149);
    line-height: 24px;
    font-weight: 400;
}
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    border-radius:50px;
    height: 48px;
    width: 48px;
    position: absolute;
}
.LoadingTodo-completeIcon{
    left: 12px;
    background:linear-gradient(90deg , #dededef7, #d2d2d2f7);
    background-size: 400% 400%;
    -webkit-animation:loadingAnimation 3s ease-in-out infinite;
            animation:loadingAnimation 3s ease-in-out infinite;
}
.LoadingTodo-deleteIcon{
    right: 10px;
    background:linear-gradient(90deg , #dededef7, #d2d2d2f7);
    background-size: 400% 400%;
    -webkit-animation:loadingAnimation 3s ease-in-out infinite;
            animation:loadingAnimation 3s ease-in-out infinite;
}

.LoadingTodo-container{
    background:linear-gradient(90deg , #e4e4e4f7, #dededef7);
    background-size: 400% 400%;
    -webkit-animation:loadingAnimation 5s ease-in-out infinite;
            animation:loadingAnimation 5s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 50% 100%;
    }
    100%{
        background-position: 0% 50%;
    }
}

@keyframes loadingAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 50% 100%;
    }
    100%{
        background-position: 0% 50%;
    }
}
